import styled from "styled-components";


export const Container = styled.div`
    display: flex;
    flex-direction: column;
    width: 500px;
    border-radius: 5px;

   
    p{
        display: flex;
        font-size: 18px;
        font-weight: bold;
        font-style: italic;
        justify-content: space-between;
        align-items: center;
        color: white;
        margin-left: 10px;
        margin-right: 10px;
    }

`;

export const Content = styled.div`
    display: flex;
    flex-direction: column;

    div{
        display: flex;
        flex: 1;
        background-color: white;
        margin: 10px;
        align-items: center;
        border-radius: 5px;
        padding-left: 5px;
        padding-right: 5px;
        
        input{
            display: flex;
            flex: 1;
            height: 35px;
            background-color: transparent;
            border: none;
            margin-left: 5px;
            margin-right: 5px;
            padding-left: 10px;
        }
    }

    span{
        display: flex;
        margin: 10px;
        justify-content: space-between;
        align-items: center;

        select{
            display: flex;
            flex: 1;
            height: 35px;
            border-radius: 5px;
        }
        button{
            background-color: #e1e1e1;
            display: flex;
            width: 120px;
            justify-content: center;
            align-items: center;
            height: 35px;
            border-radius: 5px;
            margin-right: 0px;
            margin-left: 10px;

            &:hover{
                cursor: pointer;
            }
            label{
                margin-left: 5px;

                &:hover{
                    cursor: pointer;
                }
            }
        }

    }
`;


export const Opc = styled.div`
    display: flex;
    justify-content: flex-end;

    button{
        display: flex;
        background-color: #e1e1e1;
        width: 100px;
        height: 35px;
        justify-content: center;
        align-items: center;
        border-radius: 5px;
        font-weight: bold;
        margin-right: 10px;

        &:hover{
            cursor: pointer;
        }
    }
`;