import { call, put } from "redux-saga/effects";
import { getEquipes, addEquipe, editEquipe, deleteEquipe } from "../../api/equipeService";


export function* sagaEquipes(action) {
    //console.log("teste")
    const equipes = yield call(getEquipes, action.payload);
    //console.log(equipes);
    yield put({
        type: "SET_EQUIPES",
        payload: equipes
    })
}

export function* sagaAddEquipe(action) {

    const img_base64 = yield call(convertBase64, action.payload.avatar);
    action.payload.avatar = img_base64;
    console.log(action.payload)

    const equipe = yield call(addEquipe, action.payload);
    //console.log("fim");
    const equipes = yield call(getEquipes); 
    yield put({
        type: "SET_EQUIPES",
        payload: equipes
    })
}
const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
        const fileReader = new FileReader();
        fileReader.readAsDataURL(file)
        fileReader.onload = () => {
            resolve(fileReader.result);
        }
        fileReader.onerror = (error) => {
            reject(error);
        }
    })
}

export function* sagaEditEquipe(action) {

    console.log(typeof action.payload.avatar)

    if(typeof action.payload.avatar !== "string" && action.payload.avatar !== null ){
        const img_base64 = yield call(convertBase64, action.payload.avatar);
        action.payload.avatar = img_base64;
        console.log(action.payload)
    }else if(typeof action.payload.avatar === "string"){
        action.payload.avatar = '';
    }

    const equipe = yield call(editEquipe, action.payload);
    const equipes = yield call(getEquipes);
    yield put({
        type: "SET_EQUIPES",
        payload: equipes
    })
}


export function* sagaDeleteEquipe(action) {

    const equipe = yield call(deleteEquipe, action.payload);
    const equipes = yield call(getEquipes);
    yield put({
        type: "SET_EQUIPES",
        payload: equipes
    })
}
