import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";

import { Container, Content, Opc } from "./style";

import { AiOutlineClose, AiTwotoneMail } from "react-icons/ai";
import { FaUser, FaLock, FaEye, FaEyeSlash } from "react-icons/fa";
import { MdAdd } from "react-icons/md";

import ModalPerfil from "../modalAddPerfil";

const ModalEditUser = (props) => {

    console.log(props)

    const [nome, setNome] = useState(props.user.nome)
    const [email, setEmail] = useState(props.user.email)
    const [senha, setSenha] = useState('')
    const [perfil, setPerfil] = useState(props.user.perfil_id)
    const [visiblePassword, setvisiblePassword] = useState(false)

    const [visibleModalPerfil, setVisibleModalPerfil] = useState(false)

    let perfis = []
    perfis = useSelector(state => state.perfilReducer);

    const dispatch = useDispatch()

    function editUser() {
        if (nome === '' || email === "") {
            alert("Nome e E-mail são obrigatorios")
        } else if (senha === '') {
            dispatch({
                type: "SAGA_EDIT_USER",
                payload: {
                    nome: nome,
                    email: email,
                    perfil_id: perfil,
                    id: props.user.id
                }
            })
            props.visible(false)
        } else {
            dispatch({
                type: "SAGA_EDIT_USER",
                payload: {
                    nome: nome,
                    email: email,
                    senha: senha,
                    perfil_id: perfil,
                    id: props.user.id
                }
            })
            props.visible(false)
        }
    }

    function getPerfis() {

        if (perfis === false) {
            return <span>Erro</span>
        } else if (perfis.length === 0) {
            return <span>Carregando</span>
        } else {
            return (
                <select onChange={(e) => { setPerfil(e.target.value) }} name="Perfil" id="Perfil">
                    <optgroup label="Perfil">
                        {
                            perfis.data.data.map((item) => (
                                <option
                                    value={item.id}
                                    selected = {perfil === item.id ? true : false} 
                                >
                                    {item.perfil}
                                </option>
                            ))
                        }
                    </optgroup>
                </select>
            )
        }
    }



    useEffect(() => {
        dispatch({
            type: "SAGA_SET_PERFIS"
        })
        console.log(perfis)
    }, [])

    return (
        <Modal isOpen={true} style={{
            content: {
                top: '50%',
                left: '50%',
                backgroundColor: "gray",
                right: 'auto',
                bottom: 'auto',
                marginRight: '-50%',
                transform: 'translate(-50%, -50%)'

            }
        }}>
            <Container>
            {visibleModalPerfil && <ModalPerfil visible={setVisibleModalPerfil} />}
                <p>
                    <label>EDIÇÃO DE USUÁRIO</label>
                    <AiOutlineClose onClick={() => { props.visible(false) }} size={20} />
                </p>
                <Content>
                    <div>
                        <FaUser size={20} color={"gray"} />
                        <input
                            placeholder={"Nome:"}
                            value={nome}
                            onChange={(e) => setNome(e.target.value)}
                        />
                    </div>

                    <div>
                        <AiTwotoneMail size={20} color={"gray"} />
                        <input
                            placeholder={"E-mail:"}
                            type={"email"}
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                        />
                    </div>


                    <div>
                        <FaLock size={20} color={"gray"} />
                        <input
                            placeholder={"Senha:"}
                            type={visiblePassword ? "text" : "password"}
                            value={senha}
                            onChange={(e) => setSenha(e.target.value)}
                        />
                        <label onClick={()=>{setvisiblePassword(!visiblePassword)}} >
                            {visiblePassword ? <FaEye size={20} color={"gray"} /> : <FaEyeSlash size={20} color={"gray"} />}
                        </label>
                    </div>


                    <span>
                        {
                            getPerfis()
                        }
                        <button onClick={() => { setVisibleModalPerfil(true) }} >
                            <MdAdd size={20} />
                            <label>Novo Perfil</label>
                        </button>
                    </span>

                </Content>
                <Opc>
                    <button onClick={() => { editUser() }} >SALVAR</button>
                    <button onClick={() => { props.visible(false) }}>SAIR</button>
                </Opc>
            </Container>
        </Modal >
    )
}

export default ModalEditUser;