import Axios from "axios"



export const getPerfis = (url = 'http://eventapi.ramersonrodrigues.com/api/perfil') => (
    Axios.get(url)
        .then(res => {
            console.log(res.data);
            return res
        }).catch(error => {
            console.log(error)
            return false
        })
)


export const addPerfil = (perfil) => (
    Axios.post("http://eventapi.ramersonrodrigues.com/api/perfil", {
        "perfil": perfil.perfil,
    })
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error) 
            return false
        })
)

export const editPerfil = (perfil) => (
    Axios.post("http://eventapi.ramersonrodrigues.com/api/perfil/" + perfil.id, perfil)
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error)
            return false
        })
)


export const deletePerfil = (perfil) => (
    Axios.delete("http://eventapi.ramersonrodrigues.com/api/perfil/" + perfil.id,)
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error)
            return false
        })
)

