import styled from "styled-components";

export const Card = styled.div`
    display: flex;
    flex-direction: column;
    background-color: #e1e1e1;
    width: 200px;
    border-radius: 5px;
    margin: 5px;
    padding: 10px;
`;

export const Titulo = styled.span`
    display: flex;
    flex-direction: column;
    font-size: 14px;
    font-weight: bold;
`;

export const Content = styled.div`
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-bottom: 10px;
    label{
        font-size:12px;
        margin-bottom:5px;
    }
`;

export const Opc = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    margin-top: 10px;
    div{
        display: flex;
        border: 1px solid gray;
        align-items: center;
        padding: 5px;
        border-radius: 5px;
        label{
            margin-left: 5px;
            &:hover{
            cursor: pointer;
        }
        }

        &:hover{
            cursor: pointer;
        }
    }


`;