import React, { useEffect, useState } from "react";
import { Container, Titulo, Pesquisa, Add } from "./style";

import { FaSearch } from "react-icons/fa";
import { MdAdd } from "react-icons/md";
import { useDispatch } from "react-redux";


const BarComponent = (props) => {

    const [pesq, setpesq] = useState('')

    const dispatch = useDispatch()

    function pesquisa() {
        
    }

    useEffect(()=>{
        console.log(pesq)
        dispatch({
            type: "SAGA_SET_USERS",
            payload: "http://eventapi.ramersonrodrigues.com/api/usuario?pesq="+pesq,
        })
    }, [pesq])

    return (
        <Container >
            <div>
                <Titulo>USUÁRIOS</Titulo>
                <Add onClick={() => { props.setVisibleModalAdd(true) }}><MdAdd size={25} /> <label>Novo Usuário</label> </Add>
            </div>
            <Pesquisa >
                <input
                    type="text"
                    placeholder="Buscar..."
                    value={pesq}
                    onChange={(e) => { setpesq(e.target.value); }}
                />
                <FaSearch size={20} />
            </Pesquisa>
        </Container>
    )
}

export default BarComponent 