import React, { } from "react";
import { FiEdit, FiXCircle } from "react-icons/fi";


const ItemEditEvento = (props) => {
console.log(props)
    return (
        <tr>
            <td>{props.evento.id}</td>
            <td>{props.evento.titulo}</td>
            <td>{props.evento.data} {props.evento.horario}</td>
            <td>
                <div><FiEdit onClick={() => {props.eventoEdit(props.evento); props.visibleModalEdit(true) }} /></div>
                <div><FiXCircle onClick={() => {props.eventoEdit(props.evento) ; props.visibleModalDelete(true) }} /></div>
            </td>
        </tr>
    )
}

export default ItemEditEvento;