import { call, put } from "redux-saga/effects";
import { getEventos, addEvento, editEvento, deleteEvento } from "../../api/eventoService";


export function* sagaEventos(action) {
    //console.log("teste")
    const eventos = yield call(getEventos, action.payload);
    //console.log(users);
    yield put({
        type: "SET_EVENTOS",
        payload: eventos
    })
}

function* img(list) {
    let imagens = []
    for (let img of list) {
        console.log(img)
        if (typeof img.url === "string") {
            imagens = [...imagens, { "img_base64": img.url }]
            //console.log(typeof img +" !== "+"string")

        } else {
            let img_base64 = yield call(convertBase64, img);
            imagens = [...imagens, { "img_base64": img_base64 }]
        }
    }
    return imagens;
}

export function* sagaAddEvento(action) {

    // console.log(action.payload.imagens[0])

    const list_imagens = yield call(img, action.payload.imagens)

    action.payload.imagens = list_imagens;
    const evento = yield call(addEvento, action.payload);
    //console.log("fim");
    const eventos = yield call(getEventos);
    yield put({
        type: "SET_EVENTOS",
        payload: eventos
    })
}

const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
        const fileReader = new FileReader();
        fileReader.readAsDataURL(file)
        fileReader.onload = () => {
            resolve(fileReader.result);
        }
        fileReader.onerror = (error) => {
            reject(error);
        }
    })
}


export function* sagaEditEvento(action) {

    console.log(action)

    const list_imagens = yield call(img, action.payload.imagens)

    action.payload.imagens = list_imagens;

    const evento = yield call(editEvento, action.payload);
    const eventos = yield call(getEventos);
    yield put({
        type: "SET_EVENTOS",
        payload: eventos
    })
}


export function* sagaDeleteEvento(action) {

    console.log(action)
    const evento = yield call(deleteEvento, action.payload);
    const eventos = yield call(getEventos);
    yield put({
        type: "SET_EVENTOS",
        payload: eventos
    })
}
