import React, { } from "react";
import { FiEdit, FiXCircle } from "react-icons/fi";


const ItemEditEquipe = (props) => {
console.log(props)
    return (
        <tr>
            <td>{props.equipe.id}</td>
            <td>{props.equipe.nome}</td>
            <td>{props.equipe.email}</td>
            <td>{props.equipe.funcao}</td>
            <td>
                <div><FiEdit onClick={() => {props.equipeEdit(props.equipe); props.visibleModalEdit(true) }} /></div>
                <div><FiXCircle onClick={() => {props.equipeEdit(props.equipe) ; props.visibleModalDelete(true) }} /></div>
            </td>
        </tr>
    )
}

export default ItemEditEquipe;