import React, { useState } from "react";
import Modal from "react-modal";
import { useDispatch } from "react-redux";

import { Container, Content, Opc } from "./style";

import { AiOutlineClose, } from "react-icons/ai";
import { FaUser, FaCheck} from "react-icons/fa";



const ModalAddPerfil = (props) => {

    const [perfil, setPerfil] = useState("")

    const dispatch = useDispatch()

    function addPerfil() {
        if (perfil === '') {
            alert("Preencha os dados")
        } else {
            dispatch({
                type: "SAGA_ADD_PERFIL",
                payload: {
                    perfil: perfil,
                }
            })
            props.visible(false)
        }
    }

    return (
        <Modal isOpen={true} style={{
            content: {
                top: '50%',
                left: '50%',
                backgroundColor: "gray",
                right: 'auto',
                bottom: 'auto',
                marginRight: '-50%',
                transform: 'translate(-50%, -50%)'

            }
        }}>
            <Container>
                <p>
                    <label>CADASTRO DE PERFIL</label>
                    <AiOutlineClose onClick={() => { props.visible(false) }}  size={20} />
                </p>
                <Content>
                    <div>
                        <FaUser size={20} color={"gray"} />
                        <input
                            placeholder={"Perfil:"}
                            value={perfil}
                            onChange={(e) => setPerfil(e.target.value)}
                        />
                    </div>
                </Content>
                <Opc>
                    <button onClick={() => { props.visible(false) }}><AiOutlineClose size={15} /> <label>SAIR</label></button>
                    <button onClick={() => { addPerfil() }} ><FaCheck size={15} /> <label>SALVAR</label></button>
                </Opc>
            </Container>
        </Modal >
    )
}

export default ModalAddPerfil;