import Axios from "axios"


export const getEquipes = (url = 'http://eventapi.ramersonrodrigues.com/api/equipe') => (
    Axios.get(url)
        .then(res => {
            //console.log(res.data);
            return res
        }).catch(error => {
            //console.log(error)
            return false
        })
)

export const addEquipe = (equipe) => (
    Axios.post("http://eventapi.ramersonrodrigues.com/api/equipe", {
        "avatar": equipe.avatar,
        "nome": equipe.nome,
        "email": equipe.email,
        "funcao": equipe.funcao,
        "frase" : equipe.frase,
        "facebook": equipe.facebook,
        "instagran" : equipe.instagran,
        "linkedin" : equipe.linkedin,
        "whatsapp" : equipe.whatsapp,
    })
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error) 
            return false
        })
)

export const editEquipe = (equipe) => (
    Axios.post("http://eventapi.ramersonrodrigues.com/api/equipe/" + equipe.id, equipe)
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error)
            return false
        })
)

export const deleteEquipe = (equipe) => (
    Axios.delete("http://eventapi.ramersonrodrigues.com/api/equipe/" + equipe.id,)
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error)
            return false
        })
)

