import { Container, Page, Box } from "./style";
import Bar from "../../components/bar";
import Header from "../../components/header";
import Footer from "../../components/footer";
import ModalAddUser from "./components/modalAddUser";

import BarTitulo from "./components/bar";
import Grade from "./components/grade";
import React, { useState } from "react";

const User = () => {

    const [visibleModalAdd, setVisibleModalAdd] = useState(false)

    return (
        <>
            {visibleModalAdd && <ModalAddUser visible={setVisibleModalAdd} />}
            <Container>
                <Bar />
                <Box>
                    <Header />
                    <Page>
                        <BarTitulo setVisibleModalAdd={setVisibleModalAdd} ></BarTitulo>
                        <Grade></Grade>
                    </Page>
                    <Footer />
                </Box>

            </Container>

        </>
    )
}

export default User;