import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";

import { Container } from "./style";
import { colors } from "../../../../assets/colors";

const ModalEditEvento = (props) => {

    console.log(props)

    const [titulo, setTitulo] = useState(props.evento.titulo)
    const [descricao, setDescricao] = useState(props.evento.descricao)
    const [data, setData] = useState(props.evento.data)
    const [horario, setHorario] = useState(props.evento.horario)
    const [categoria_id, setCategoria_id] = useState(props.evento.categoria_id)

    const [cidade, setCidade] = useState(props.evento.endereco.cidade)
    const [bairro, setBairro] = useState(props.evento.endereco.bairro)
    const [rua, setRua] = useState(props.evento.endereco.rua)
    const [numero, setNumero] = useState(props.evento.endereco.numero)
    const [complemento, setComplemento] = useState(props.evento.endereco.complemento)
    const [latitude, setlatitude] = useState(props.evento.endereco.latitude)
    const [longitude, setLongitude] = useState(props.evento.endereco.longitude)

    const [imagens, setImagens] = useState(props.evento.imagens)

    const dispatch = useDispatch()

    let categorias = []
    categorias = useSelector(state => state.categoriaReducer);

    useEffect(() => {
        dispatch({
            type: "SAGA_SET_CATEGORIAS"
        })
    }, [])


    function editEvento() {
        if (
            titulo === '' || descricao === "" || data === "" || horario === "" || categoria_id === "" ||
            cidade === '' || bairro === "" || rua === "" || numero === "" || latitude === "" || longitude === ""
        ) {
            alert("Preencha todos os dados")
        } else {
            //alert( titulo + descricao + data + horario + categoria_id )
            //alert( cidade + bairro + rua + numero + complemento + latitude + longitude )
            dispatch({
                type: "SAGA_EDIT_EVENTO",
                payload: {
                    titulo: titulo,
                    descricao: descricao,
                    data: data,
                    horario: horario,
                    categoria_id: categoria_id,

                    cidade: cidade,
                    bairro: bairro,
                    rua: rua,
                    numero: numero,
                    complemento: complemento,
                    latitude: latitude,
                    longitude: longitude,

                    imagens: imagens,

                    id: props.evento.id
                }
            })
            props.visible(false)
        }
    }

    function ListItem(data) {
        console.log("lista")
        console.log(data.data.id)
        console.log(categoria_id)
        return (
            data.data.id === categoria_id ?
                <option selected value={data.data.id} >{data.data.categoria}</option> :
                <option value={data.data.id} >{data.data.categoria}</option>
        );
    }


    function pegarCategorias() {

        if (categorias === false) {
            return ""
        } else if (categorias.length === 0) {
            return ""
        } else {
            return (
                categorias.data.data.map((item) => {
                    return (
                        <ListItem data={item} />
                    )
                })
            )
        }
    }

    function removeImg(name) {
        let imgs = []
        imagens.forEach(element => {
            if (typeof element.url === "string" && element.url !== name) {
                //console.log("url: "+element.url)
                imgs = [...imgs, element]
            }
            if (typeof element.url === "undefined" && element.name !== name) {
                //console.log("url: "+element.name)
                imgs = [...imgs, element]
            }
            
        });
        setImagens(imgs)
    }


    return (
        <Modal isOpen={true} style={{
            content: {
                backgroundColor: colors.primaryColor,
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
            }
        }}>
            <Container>
                <input
                    value={''}
                    type="file"
                    accept=".jpeg, .png, .jpg "
                    onChange={(e) => {
                        setImagens([...imagens, e.target.files[0]]);

                    }}
                />
                <span>

                    {
                        imagens.map((item) => {
                            return (
                                typeof item.url === "string" ?
                                    <img name={item.url} onClick={(e) => { removeImg(e.target.name) }} src={item.url} /> :
                                    <img name={item.name} onClick={(e) => { removeImg(e.target.name) }} src={URL.createObjectURL(item)} />
                            )
                        })
                    }
                </span>
                <input
                    placeholder={"Titulo:"}
                    value={titulo}
                    onChange={(e) => setTitulo(e.target.value)}
                />
                <input
                    placeholder={"Descrição:"}
                    value={descricao}
                    onChange={(e) => setDescricao(e.target.value)}
                />
                <input
                    placeholder={"Data:"}
                    type={"date"}
                    value={data}
                    onChange={(e) => setData(e.target.value)}
                />
                <input
                    placeholder={"Horario:"}
                    type={"time"}
                    value={horario}
                    onChange={(e) => setHorario(e.target.value)}
                />

                <select onChange={(e) => { setCategoria_id(e.target.value) }} name="Categoria" id="Categoria">
                    <optgroup label="Categoria">
                        {
                            pegarCategorias()
                        }

                    </optgroup>
                </select>

                <input
                    placeholder={"Cidade:"}
                    value={cidade}
                    onChange={(e) => setCidade(e.target.value)}
                />
                <input
                    placeholder={"Bairro:"}
                    value={bairro}
                    onChange={(e) => setBairro(e.target.value)}
                />
                <input
                    placeholder={"Rua:"}
                    value={rua}
                    onChange={(e) => setRua(e.target.value)}
                />
                <input
                    placeholder={"Numero:"}
                    value={numero}
                    onChange={(e) => setNumero(e.target.value)}
                />
                <input
                    placeholder={"Complemento:"}
                    value={complemento}
                    onChange={(e) => setComplemento(e.target.value)}
                />
                <input
                    placeholder={"Latitude:"}
                    value={latitude}
                    onChange={(e) => setlatitude(e.target.value)}
                />
                <input
                    placeholder={"Longitude:"}
                    value={longitude}
                    onChange={(e) => setLongitude(e.target.value)}
                />

                <button onClick={() => { editEvento() }} >SALVAR</button>
                <button onClick={() => { props.visible(false) }}>SAIR</button>
            </Container>
        </Modal>
    )
}

export default ModalEditEvento;