import { call, put } from "redux-saga/effects";
import { getPerfis, addPerfil, editPerfil, deletePerfil,  } from "../../api/perfilService";


export function* sagaPerfis(action) {
    console.log("teste")
    const perfis = yield call(getPerfis, action.payload);
    console.log(perfis);
    yield put({
        type: "SET_PERFIS",
        payload: perfis
    })
}

export function* sagaAddPerfil(action) {
    console.log(action.payload)
    const perfil = yield call(addPerfil, action.payload);
    const perfis = yield call(getPerfis);
    yield put({
        type: "SET_PERFIS",
        payload: perfis
    })
}

export function* sagaEditPerfil(action) {
    console.log("teste")
    const perfil = yield call(editPerfil, action.payload);
    const perfis = yield call(getPerfis);
    yield put({
        type: "SET_PERFIS",
        payload: perfis
    })
}

export function* sagaDeletePerfil(action) {

    const perfil = yield call(deletePerfil, action.payload);
    const perfis = yield call(getPerfis);
    yield put({
        type: "SET_PERFIS",
        payload: perfis
    })
}


