import React, { useState } from "react";
import Modal from "react-modal";
import { useDispatch } from "react-redux";

import { Container } from "./style";
import { colors } from "../../../../assets/colors";

import { BsPersonBoundingBox } from "react-icons/bs";


const ModalAddEquipe = (props) => {

    const [avatar, setAvatar] = useState(null)
    const [nome, setNome] = useState("")
    const [email, setEmail] = useState("")
    const [funcao, setFuncao] = useState("")
    const [frase, setFrase] = useState("")
    const [facebook, setFacebook] = useState("")
    const [instagran, setInstagran] = useState("")
    const [linkedin, setLinkedin] = useState("")
    const [whatsapp, setWhatsapp] = useState("")

    const dispatch = useDispatch()

    function addEquipe() {
        //alert(nome+email+funcao+frase+facebook+instagran+linkedin+whatsapp)
        if (nome === '' || email === "" || funcao === "" || frase === "" || facebook === '' || instagran === "" || linkedin === "" || whatsapp === "") {
            alert("Preencha todos os dados")
        } else {
            dispatch({
                type: "SAGA_ADD_EQUIPE",
                payload: {
                    avatar: avatar,
                    nome: nome,
                    email: email,
                    funcao: funcao,
                    frase: frase,
                    facebook: facebook,
                    instagran: instagran,
                    linkedin: linkedin,
                    whatsapp: whatsapp
                }
            })
            props.visible(false)
        }
    }

    return (
        <Modal isOpen={true} style={{
            content: {
                backgroundColor: colors.primaryColor,
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
            }
        }}>
            <Container>
                <p>CADASTRO DE EQUIPE</p>
                <span> 
                    {
                        avatar === null ?
                            <BsPersonBoundingBox size={75} color={"white"} /> :
                            <img src={URL.createObjectURL(avatar)} />
                    }
                    <input
                        type="file"
                        accept = ".jpeg, .png, .jpg "
                        onChange={(e) => { console.log(e.target.files); setAvatar(e.target.files[0]) }}
                    />
                </span>
                <input
                    placeholder={"Nome:"}
                    value={nome}
                    onChange={(e) => setNome(e.target.value)}
                />
                <input
                    placeholder={"E-mail:"}
                    type={"email"}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <input
                    placeholder={"Função:"}
                    value={funcao}
                    onChange={(e) => setFuncao(e.target.value)}
                />
                <input
                    placeholder={"Frase:"}
                    value={frase}
                    onChange={(e) => setFrase(e.target.value)}
                />
                <input
                    placeholder={"Facebook:"}
                    value={facebook}
                    onChange={(e) => setFacebook(e.target.value)}
                />
                <input
                    placeholder={"Instagran:"}
                    value={instagran}
                    onChange={(e) => setInstagran(e.target.value)}
                />
                <input
                    placeholder={"Linkedin:"}
                    value={linkedin}
                    onChange={(e) => setLinkedin(e.target.value)}
                />
                <input
                    placeholder={"Whatsapp:"}
                    value={whatsapp}
                    onChange={(e) => setWhatsapp(e.target.value)}
                />

                <button onClick={() => { addEquipe() }} >SALVAR</button>
                <button onClick={() => { props.visible(false) }}>SAIR</button>
            </Container>
        </Modal>
    )
}

export default ModalAddEquipe;