import React, { useEffect, useState } from "react";
import { Container, Pages, } from "./style";

import {  FiPlus, } from "react-icons/fi";
import { IoMdArrowDropleft, IoMdArrowDropright } from "react-icons/io";
import { useDispatch, useSelector } from "react-redux";

import ItemEvento from "../itemEvento";
import ModalAddEvento from "../modalAddEvento";
import ModalEditEvento from "../modalEditEvento";
import ModalDeleteEvento from "../modalDeleteEvento";

const Grade = () => {

    let eventos = []
    const dispatch = useDispatch();
    eventos = useSelector(state => state.eventoReducer);

    const [visibleModalDelete, setVisibleModalDelete] = useState(false)
    const [visibleModalAdd, setVisibleModalAdd] = useState(false)
    const [visibleModalEdit, setVisibleModalEdit] = useState(false)
    const [eventoEdit, setEventoEdit] = useState(false)

    useEffect(() => {
        dispatch({
            type: "SAGA_SET_EVENTOS"
        })
    }, [])

    function dados() {
        if (eventos === false) {
            return <span>Erro</span>
        } else if (eventos.length === 0) {
            return <span>Carregando</span>
        } else {
            return (
                eventos.data.data.map((item) => (
                    <ItemEvento
                        visibleModalEdit={setVisibleModalEdit}
                        visibleModalDelete={setVisibleModalDelete}
                        eventoEdit= {setEventoEdit}
                        evento={item}
                        key={"" + item.id} />
                ))
            )
        }
    }

    function pages() {
        if (eventos === false) { 
            return <span>Erro</span>
        } else if (eventos.length === 0) {
            return <span>Carregando</span>
        } else {
            //console.log(eventos.data)
            return (
                <Pages>
                    <div>
                        <IoMdArrowDropleft size={30} onClick={() => {
                            eventos.data.prev_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: eventos.data.prev_page_url })
                        }} />
                    </div>
                    <span>
                        {
                            eventos.data.to + ' de ' + eventos.data.total
                        }
                    </span>
                    <div>
                        <IoMdArrowDropright size={30} onClick={() => {
                            eventos.data.next_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: eventos.data.next_page_url })
                        }} />
                    </div>
                </Pages>
            )
        }
    }

    return (
        <Container>
            {visibleModalDelete && <ModalDeleteEvento visible={setVisibleModalDelete} evento={eventoEdit}  />}
            {visibleModalAdd && <ModalAddEvento visible={setVisibleModalAdd}  />}
            {visibleModalEdit && <ModalEditEvento visible={setVisibleModalEdit} evento={eventoEdit} />}
            <table >
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Nome</th>
                        <th>E-mail</th>
                        <th>
                            <div><FiPlus onClick={() => { setVisibleModalAdd(true) }} /></div>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {dados()}
                </tbody>
            </table>
            {pages()}
        </Container >

    )
}

export default Grade;