import styled from "styled-components";


export const Container = styled.div`
    height: 50px;
    display: flex;
    flex-direction: row;
    margin:10px;
    justify-content: space-between;
    align-items: center;
    padding-left:10px;
    padding-right:10px;
    font-weight: bold;
    font-style: italic;
    font-size: 18px;
    background-color: #E1E1E1;
    color: gray;
    border-radius:5px;
    div{
        display: flex;
        align-items: center;
    }
`;

export const Titulo = styled.span`
    display: flex;
    width: 100px;
    margin-right: 10px;
    margin-left: 10px;
`;
export const Pesquisa = styled.div`

    display: flex;
    border:solid 1px;
    border-radius:15px;
    width:270px;
    align-items: center;
    justify-content: flex-end;
    padding-right: 10px;

    input, input:focus {
        display: flex;
        background-color:transparent;
        font-size:16px;
        border:none;
        height:30px;
        width:250px;
        outline: none;
        margin-left:10px;
        margin-right: 10px;
    }

`;

export const Add = styled.a`
    background-color: gray;
    display: flex;
    align-items: center;
    border-radius: 10px;
    padding: 5px;
    color:  #E1E1E1;

    &:hover{
        cursor: pointer;
    }

    label{
        margin-left:5px;
        margin-right:5px;

        &:hover{
            cursor: pointer;
        }
    }
    
`;