import React, { } from "react";
import { FaTrash, FaEdit } from "react-icons/fa";
import { Card, Titulo, Content, Opc } from "./style";


const ItemEditUser = (props) => {
    console.log(props)
    return (
        <Card>
            <Titulo>{props.user.id} - {props.user.perfil.perfil}</Titulo>
            <Content>
                <label>{props.user.nome}</label>
                <label>{props.user.email}</label>
            </Content>
            <Opc>
                <div onClick={() => { props.userEdit(props.user); props.visibleModalEdit(true) }}>
                    <FaEdit size={20} />
                    <label>EDITAR</label>
                </div>
                <div onClick={() => { props.userEdit(props.user); props.visibleModalDelete(true) }}>
                    <FaTrash size={15} />
                    <label>APAGAR</label>
                </div>
            </Opc>
        </Card>
    )
}

export default ItemEditUser;