import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";

import { Container } from "./style";
import { colors } from "../../../../assets/colors";


const ModalAddEvento = (props) => {

    const [titulo, setTitulo] = useState("")
    const [descricao, setDescricao] = useState("")
    const [data, setData] = useState("")
    const [horario, setHorario] = useState("")
    const [categoria_id, setCategoria_id] = useState('')

    const [cidade, setCidade] = useState("")
    const [bairro, setBairro] = useState("")
    const [rua, setRua] = useState("")
    const [numero, setNumero] = useState("")
    const [complemento, setComplemento] = useState('')
    const [latitude, setlatitude] = useState("")
    const [longitude, setLongitude] = useState('')

    const [imagens, setImagens] = useState([])
    
    const dispatch = useDispatch()

    let categorias = []
    categorias = useSelector(state => state.categoriaReducer);

    useEffect(() => {
        dispatch({
            type: "SAGA_SET_CATEGORIAS"
        })
    }, [])

    function addEvento() {
        console.log(categorias)
        if (
            titulo === '' || descricao === "" || data === "" || horario === "" || categoria_id === "" ||
            complemento === '' || cidade === '' || bairro === "" || rua === "" || numero === "" || latitude === "" || longitude === ""
        ) {
            alert("Preencha todos os dados")
        } else {
            //alert(titulo + descricao + data + horario + categoria_id)
            //alert(cidade + bairro + rua + numero + complemento + latitude + longitude)
            dispatch({
                type: "SAGA_ADD_EVENTO",
                payload: {
                    titulo: titulo,
                    descricao: descricao,
                    data: data,
                    horario: horario,
                    categoria_id: categoria_id,

                    cidade: cidade,
                    bairro: bairro,
                    rua: rua,
                    numero: numero,
                    complemento: complemento,
                    latitude: latitude,
                    longitude: longitude,
                    imagens: imagens
                }
            })
            props.visible(false)
        }
    }

    function pegarCategorias() {

        if (categorias === false) {
            return ""
        } else if (categorias.length === 0) {
            return ""
        } else {
            return (
                categorias.data.data.map((item) => {
                    return (
                        <option value={item.id} >{item.categoria}</option>
                    )
                })
            )
        }
    }

    function removeImg(name){
        let imgs = []
        imagens.forEach(element => {
            if(element.name !== name){
                imgs = [...imgs, element]
            }
        });
        setImagens(imgs)
    }

    return (
        <Modal isOpen={true} style={{
            content: {
                backgroundColor: colors.primaryColor,
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
            }
        }}>
            <Container>
                <p>CADASTRO DE EVENTO</p>
                <input
                    value={''}
                    type="file"
                    accept=".jpeg, .png, .jpg "
                    onChange={(e) => {
                        setImagens([...imagens, e.target.files[0]]);
                        
                    }}
                />
                <span>
                    {
                        imagens.map((item) => {
                            return (
                                <img name={item.name} onClick={(e)=>{removeImg(e.target.name)}}  src={URL.createObjectURL(item)} />
                            )
                        })
                    }
                </span>
                <input
                    placeholder={"Titulo:"}
                    value={titulo}
                    onChange={(e) => setTitulo(e.target.value)}
                />
                <input
                    placeholder={"Descrição:"}
                    value={descricao}
                    onChange={(e) => setDescricao(e.target.value)}
                />
                <input
                    placeholder={"Data:"}
                    type={"date"}
                    value={data}
                    onChange={(e) => setData(e.target.value)}
                />
                <input
                    placeholder={"Horario:"}
                    type={"time"}
                    value={horario}
                    onChange={(e) => setHorario(e.target.value)}
                />

                <select onChange={(e) => { setCategoria_id(e.target.value) }} name="Categoria" id="Categoria">
                    <optgroup label="Categoria">
                        <option value={categoria_id} ></option>
                        {
                            pegarCategorias()
                        }

                    </optgroup>
                </select>

                <input
                    placeholder={"Cidade:"}
                    value={cidade}
                    onChange={(e) => setCidade(e.target.value)}
                />
                <input
                    placeholder={"Bairro:"}
                    value={bairro}
                    onChange={(e) => setBairro(e.target.value)}
                />
                <input
                    placeholder={"Rua:"}
                    value={rua}
                    onChange={(e) => setRua(e.target.value)}
                />
                <input
                    placeholder={"Numero:"}
                    value={numero}
                    onChange={(e) => setNumero(e.target.value)}
                />
                <input
                    placeholder={"Complemento:"}
                    value={complemento}
                    onChange={(e) => setComplemento(e.target.value)}
                />
                <input
                    placeholder={"Latitude:"}
                    value={latitude}
                    onChange={(e) => setlatitude(e.target.value)}
                />
                <input
                    placeholder={"Longitude:"}
                    value={longitude}
                    onChange={(e) => setLongitude(e.target.value)}
                />

                <button onClick={() => { addEvento() }} >SALVAR</button>
                <button onClick={() => { props.visible(false) }}>SAIR</button>
            </Container>
        </Modal>
    )
}

export default ModalAddEvento;