import React, { useState } from "react";
import Modal from "react-modal";
import { useDispatch } from "react-redux";

import { Container } from "./style";
import { colors } from "../../../../assets/colors";

import { BsPersonBoundingBox } from "react-icons/bs";

const ModalEditEquipe = (props) => {

    console.log(props)

    const dispatch = useDispatch()

    const [avatar, setAvatar] = useState(props.equipe.avatar)
    const [nome, setNome] = useState(props.equipe.nome)
    const [email, setEmail] = useState(props.equipe.email)
    const [funcao, setFuncao] = useState(props.equipe.funcao)
    const [frase, setFrase] = useState(props.equipe.frase)
    const [facebook, setFacebook] = useState(props.equipe.facebook)
    const [instagran, setInstagran] = useState(props.equipe.instagran)
    const [linkedin, setLinkedin] = useState(props.equipe.linkedin)
    const [whatsapp, setWhatsapp] = useState(props.equipe.whatsapp)

    function editEquipe() {
        if (nome === '' || email === "" || funcao === "" || frase === "" || facebook === '' || instagran === "" || linkedin === "" || whatsapp === "") {
            alert("Preencha os campos")
        } else {
            dispatch({
                type: "SAGA_EDIT_EQUIPE",
                payload: {
                    avatar: avatar,
                    nome: nome,
                    email: email,
                    funcao: funcao,
                    frase: frase,
                    facebook: facebook,
                    instagran: instagran,
                    linkedin: linkedin,
                    whatsapp: whatsapp,
                    id: props.equipe.id
                }
            })
            props.visible(false)
        }
    }

    return (
        <Modal isOpen={true} style={{
            content: {
                backgroundColor: colors.primaryColor,
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
            }
        }}>
            <Container>
                <p>Atualização DE EQUIPE</p>
                <span>
                    {
                        avatar === null ?
                            <BsPersonBoundingBox size={75} color={"white"} /> :
                            typeof avatar === "string" ?
                                <img src={avatar} /> :
                                <img src={URL.createObjectURL(avatar)} />
                    }
                    <input
                        type="file"
                        accept=".jpeg, .png, .jpg "
                        onChange={(e) => { console.log(e.target.files); setAvatar(e.target.files[0]) }}
                    />
                </span>
                <input
                    placeholder={"Nome:"}
                    value={nome}
                    onChange={(e) => setNome(e.target.value)}
                />
                <input
                    placeholder={"E-mail:"}
                    type={"email"}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <input
                    placeholder={"Função:"}
                    value={funcao}
                    onChange={(e) => setFuncao(e.target.value)}
                />
                <input
                    placeholder={"Frase:"}
                    value={frase}
                    onChange={(e) => setFrase(e.target.value)}
                />
                <input
                    placeholder={"Facebook:"}
                    value={facebook}
                    onChange={(e) => setFacebook(e.target.value)}
                />
                <input
                    placeholder={"Instagran:"}
                    value={instagran}
                    onChange={(e) => setInstagran(e.target.value)}
                />
                <input
                    placeholder={"Linkedin:"}
                    value={linkedin}
                    onChange={(e) => setLinkedin(e.target.value)}
                />
                <input
                    placeholder={"Whatsapp:"}
                    value={whatsapp}
                    onChange={(e) => setWhatsapp(e.target.value)}
                />
                <button onClick={() => { editEquipe() }} >SALVAR</button>
                <button onClick={() => { props.visible(false) }}>SAIR</button>
            </Container>
        </Modal>
    )
}

export default ModalEditEquipe;