import React, { useEffect, useState } from "react";
import { Container, Pages, } from "./style";

import { FiPlus, } from "react-icons/fi";
import { IoMdArrowDropleft, IoMdArrowDropright } from "react-icons/io";
import { useDispatch, useSelector } from "react-redux";

import ItemEquipe from "../itemEquipe";
import ModalAddEquipe from "../modalAddEquipe";
import ModalEditEquipe from "../modalEditEquipe";
import ModalDeleteEquipe from "../modalDeleteEquipe";

const Grade = () => {

    let equipes = []
    const dispatch = useDispatch();
    equipes = useSelector(state => state.equipeReducer);

    const [visibleModalDelete, setVisibleModalDelete] = useState(false)
    const [visibleModalAdd, setVisibleModalAdd] = useState(false)
    const [visibleModalEdit, setVisibleModalEdit] = useState(false)
    const [equipeEdit, setEquipeEdit] = useState(false)

    useEffect(() => {

        dispatch({
            type: "SAGA_SET_EQUIPES"
        })
    }, [])

    function dados() {
        if (equipes === false) {
            return <span>Erro</span>
        } else if (equipes.length === 0) {
            return <span>Carregando</span>
        } else {
            return (
                equipes.data.map((item) => (
                    <ItemEquipe
                        visibleModalEdit={setVisibleModalEdit}
                        visibleModalDelete={setVisibleModalDelete}
                        equipeEdit={setEquipeEdit}
                        equipe={item}
                        key={"" + item.id} />
                ))
            )
        }
    }

    function pages() {
        if (equipes === false) {
            return <span>Erro</span>
        } else if (equipes.length === 0) {
            return <span>Carregando</span>
        } else {
            console.log(equipes.data)
            return (
                <Pages>
                    <div>
                        <IoMdArrowDropleft size={30} onClick={() => {
                            //equipes.data.prev_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: users.data.prev_page_url })
                        }} />
                    </div>
                    <span>
                        {
                            //users.data.to + ' de ' + users.data.total
                        }
                    </span>
                    <div>
                        <IoMdArrowDropright size={30} onClick={() => {
                            //users.data.next_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: users.data.next_page_url })
                        }} />
                    </div>
                </Pages>
            )
        }
    }

    return (
        <Container>
            {visibleModalDelete && <ModalDeleteEquipe visible={setVisibleModalDelete} equipe={equipeEdit} />}
            {visibleModalAdd && <ModalAddEquipe visible={setVisibleModalAdd} />}
            {visibleModalEdit && <ModalEditEquipe visible={setVisibleModalEdit} equipe={equipeEdit} />}
            <table >
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Nome</th>
                        <th>E-mail</th>
                        <th>Função</th>
                        <th>
                            <div><FiPlus onClick={() => { setVisibleModalAdd(true) }} /></div>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {
                        dados()
                    }
                </tbody>
            </table>
            {
                pages()
            }
        </Container >

    )
}

export default Grade;