import styled from "styled-components";
import { colors } from "../../assets/colors";

export const Container = styled.div`
    //background-color: #212529;
    height: 100vh;
    display: flex;
    flex-direction: column;
    width: 150px;
    align-items: center;
    border-right: 1px solid gray;
`;

export const BoxLogo = styled.div`
background-color: green;
    display: flex;
    width: 130px;
    height: 80px;
    border-radius: 10px;
    margin: 10px;
    justify-content: center;
    align-items: center;

`; 

export const Opc = styled.a`
    color: gray;
    display: flex;
    margin:5px;
    font-size: 16px;
    align-items: center;
    border-radius: 10px;
    padding:5px;

    &:hover {background: #f4f3f7; cursor: pointer; font-weight: bold}

    i{
        margin-left: 10px;
    }

`;

export const Menu = styled.div`
    display: flex;
    flex-direction: column;
    width: 125px;
    border-radius: 10px;
    padding: 10px;
    margin-top: 25px;
`;